<template>
  <div class="main">
    <div class="page-example3">
      <vue-seamless-scroll :data="listData" :class-option="defaultOption" >
        <ul class="ul-scoll">
          <li v-for="(item, index) in listData" :key='index'>
              <img src="../assets/logo.png" alt="" class="iimg"><span class="title">{{item.title}}</span><span class="date">{{item.time}}</span>
          </li>
        </ul>
      </vue-seamless-scroll>
    </div>
  </div>
</template>
 
<script>
import vueSeamlessScroll from 'vue-seamless-scroll'
export default {
    name: 'Example3',
    components: {
        vueSeamlessScroll
    },
    data() {
      // 
      return {
      listData: [
        { 
          'title': '1',
          'time': '2017-12-16'
        }, 
        {
          'title': '2',
          'time': '2017-12-16'
        }, 
        {
          'title': '3',
          'time': '2017-12-16'
        }, 
        {
          'title': '4',
          'time': '2017-12-16'
        }, 
        {
          'title': '5',
          'time': '2017-12-16'
        }, 
        {
          'title': '6',
          'time': '2017-12-16'
        }, 
        {
          'title': '7',
          'time': '2017-12-16'
        }, 
        {
          'title': '8',
          'time': '2017-12-16'
        }, 
        {
          'title': '9',
          'time': '2017-12-16'
        },
        {
          'title': '10',
          'time': '2017-12-16'
        }]
      }
    },
    //  data
    computed: {
      defaultOption () {
        return {
          step: 3, // 
          limitMoveNum: 10, //  this.dataList.length
          hoverStop: true, // stop
          direction: 1, // 0 1 2 3
          openWatch: true, // dom
          singleHeight: 0, // (0) direction => 0/1
          singleWidth: 0, // (0) direction => 2/3
          waitTime: 1000 // (1000ms)
        }
      }

    },
    // 
    methods: {},
    // data
    watch: {},
    //  - this
    created() {

    },
    //  - DOM
    mounted() {
    }
}
</script>
 
