<template>
  <div class='digitalFlop'>
    <dv-digital-flop :config="config"  style="height:70px;margin-top:5px"/>
  </div>
</template>

<script>
import {uptrafficNum} from'../../api/api'
export default {
  data() {
  //
    return {
      config:{
        number:[],
        content: '{nt}',
        textAlign:'left',
        style:{
          fontSize:70,
          fill:'rgb(77, 140, 223)'
        }
      },
      totalNum:{
        number:[],
        content: '{nt}',
        textAlign:'left',
        style:{
          fontSize:70,
          fill:'rgb(77, 140, 223)'
        }
      }
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    uupBagNum(){
      var that = this
      uptrafficNum().then(res =>{
        that.totalNum.number[0] = res.data.data
        that.config = {...that.totalNum}
      })
      setInterval(()=>{
        setTimeout(()=>{
          uptrafficNum().then(res =>{
            that.totalNum.number[0] = res.data.data
            that.config = {...that.totalNum}
          },0)
        },0)
      },180000)
    }
  },
  // - this
  created() {
    
  },
  // - DOM
  mounted() {
    this.uupBagNum()
  },
}
</script>
<style lang="less">
.digitalFlop{
  width: 100%;
}

</style>