<template>
  <div class='levelfigure'>
    <div id="leveltu2">

    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import {progressPool} from '../../api/api'
export default {
  data() {
  //
    return {
      num:0,
      ccc:''
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    bbar(){
      var myChart = echarts.init(document.getElementById("leveltu2"))
      // let imgSrc = '../assets/logo.png';
      var option2 = {
    title: [{
        text: this.ccc,
        x: '50%',
        y: '37%',
        textAlign: 'center',
        textStyle: {
            fontSize: '40',
            fontWeight: '500',
            color: '#98b5d2',
            textAlign: 'center',
        },
    }, {
        text: '',
        left: '48%',
        top: '62%',
        textAlign: 'center',
        textStyle: {
            fontSize: '20',
            fontWeight: '600',
            color: 'rgb(75, 139, 221)',
            textAlign: 'center',
        },
    }, ],
    graphic: {
        elements: [{
            type: "image",
            z: 0,
            style: {
                width: 210,
            },
            left: 'center',
            top: "30%",
        }]
    },
    angleAxis: {
        max: 100,
        clockwise: true,
        axisLine: {
            show: false
        },
        axisTick: {
            show: false
        },
        axisLabel: {
            show: false
        },
        splitLine: {
            show: false
        }
    },
    radiusAxis: {
        type: 'category',
        axisLine: {
            show: false
        },
        axisTick: {
            show: false
        },
        axisLabel: {
            show: false
        },
        splitLine: {
            show: false
        }
    },
    polar: {
        center: ['50%', '50%'],
        radius: '160.1%'
    },
    series: [
        {
            type: 'bar',
            startAngle: 100,
            clockwise: true,
            data: [{
                name: 'Total Downloads',
                value: this.num,
                itemStyle: {
                    normal: {
                        color: { // 
                                colorStops: [{
                                    offset: 0,
                                    color: '#4FADFD' // 0% 
                                }, {
                                    offset: 1,
                                    color: '#28E8FA' // 100% 
                                }]
                            },
                    }
                },
            }],
            coordinateSystem: 'polar',
            roundCap: true,
            barWidth: 15,
            barGap: '-100%',
            z: 20,
        },
        {
            type: 'bar',
            data: [{
                value: 100,
                itemStyle: {
                    color: '#223042',
                    shadowColor: 'rgba(251, 46, 99, 0.1)',
                    shadowBlur: 10,
                }
            }],
            coordinateSystem: 'polar',
            roundCap: true,
            barWidth: 15,
            barGap: '-100%',
            z: 1
        },
    ]
};
      myChart.setOption(option2);
    },
    aaar(){
      var that = this
      progressPool().then(res =>{
        var ppol = (res.data[0].value)*1000
        var bbol = ppol
        var ccol = bbol.toString().slice(0,2)
        that.num = ccol
        var gou = '%'
        that.ccc = ccol.toString() + gou
        that.bbar()
      })
      setInterval(()=>{
        progressPool().then(res =>{
          var ppol = (res.data[0].value)*1000
          var bbol = ppol
          var ccol = bbol.toString().slice(0,2)
          that.num = ccol
          var gou = '%'
          that.ccc = ccol.toString() + gou
          that.bbar()
        })
      },3000000)
    }
  },
  // - this
  created() {

  },
  // - DOM
  mounted() {
    this.bbar();
    this.aaar()
  },
//beforeCreate() {}, // - 
//beforeMount() {}, // - 
//beforeUpdate() {}, // - 
//updated() {}, // - 
//beforeDestroy() {}, // - 
//destroyed() {}, // - 
//activated() {}, //keep-alive
}
</script>
<style lang="less">
.main{
  width: 100%;
  height: 100%;
  
  #leveltu2{
    width: 210px;//525
    height: 210px;//260
    margin-top: 20px;

  }
}
</style>