        lineWidth:20,
        radius:80,
        digitalFlopStyle:{
          style:{
            textAlign: 'top',
            height: 80,
            translate: [1, 100],
            fontSize: 10,
          },
          toFixed:1,
          rowGap:1000,
          lineHeight: 10,
          fill: '#fff'
        },
        activeRadius:85 ,
        data:[
          {
            name: '',
            value: 55,
          },
          {
            name: '',
            value: 120
          },
          {
            name: '',
            value: 78
          },
          {
            name: '',
            value: 66
          },
          {
            name: '',
            value: 80
          }
      ]
      }
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    getlevel(){
      var that = this
      statistical().then(res=>{
        var citylevel = []
        for(var i = 0;i<res.data.data.length;i++){
          var zu = {}
          zu.name = res.data.data[i].area
          zu.value = res.data.data[i].proportion
          citylevel.push(zu)
        }
        that.config2.data = citylevel
        that.config = {...that.config2}
      })
      setInterval(()=>{
        setTimeout(()=>{
          statistical().then(res=>{
            var citylevel = []
            for(var i = 0;i<res.data.data.length;i++){
              var zu = {}
              zu.name = res.data.data[i].area
              zu.value = res.data.data[i].proportion
              citylevel.push(zu)
            }
            that.config2.data = citylevel
            that.config = {...that.config2}
          }) 
        },0)
      },3600000)
    }
  },
  // - this
  created() {
    this.getlevel()
  },
  // - DOM
  mounted() {
  },
}
</script>
<style lang="less">
.levelfigure{
width: 220px;
