<template>
  <div class='Usergrowth'>
    <div id="growthtu">

    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import {getusergrowth} from '.././api/api'
export default {
  data() {
  //
    return {
      num:20,
      xday:[],
      yvone:[],
      yvtwo:[],
      nima:[0, 835, 857, 923, 1304, 1428, 997, 894, 975, 990, 1019, 1363, 1700, 1323, 1207, 1211]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    bbar(){
      var myChart = echarts.init(document.getElementById("growthtu"))
      var colors=['rgb(211, 119, 58)', 'rgba(25,163,223,1)','rgb(58, 175, 54)']
      var option = option = {
          color:colors,
          legend:{
            icon:'circle',
            top:20,
            textStyle:{
              color:'aliceblue'
            }
          },
         title:{
            padding:[25,0,0,20],
            text:'',
            textStyle:{
              color : 'aliceblue',
              fontSize:25,
            },
            subtextStyle:{
              color : 'red'
            },
          },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            axisLine: {
              show: false,
              lineStyle: {
                color: 'rgb(243, 237, 227)'
              },
              symbol: ['none', 'arrow'],
              symbolSize: [5, 12],
              symbolOffset: [0, 10]
            },
            
            type: 'category',
            data: this.xday,
            axisTick:{
                    show:false
                  },
          }
        ],
        yAxis: [
          {
            axisLabel:{
              show:false
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: 'rgba(39, 57, 75, 1)',
                width: 1,
                type: 'solid'
              }
            },
            axisTick:{
              show:false
            },
            type: 'value'
          }
        ],
        series: [
          {
            name: '',
            type: 'bar',
            barWidth: '60%',
            data: this.yvtwo,
            
            color: { // 
              colorStops: [{
                  offset: 0,
                  color: 'rgb(252, 151, 0)' // 0% 
              }, {
                  offset: 1,
                  color: '#f5ef25' // 100% 
              }]
            },
          },
          {

            name: '',
            type: 'bar',
            color: { // 
              colorStops: [{
                  offset: 0,
                  color: 'rgb(117, 106, 89)' // 0% 
              }, {
                  offset: 1,
                  color: 'rgb(117, 106, 89)' // 100% 
              }]
            },
            barWidth: '60%',
            data: this.yvone,
          }, 
        ]
      };
      option && myChart.setOption(option);
    },
    ggetusergrowth(){
      var that = this
      getusergrowth().then(res=>{
        var e =['']
        var g =[]
        var h =[0]
        for(var a = 0 ; a<res.data.data.length ; a++){
          var b = ''
          var c = parseInt(res.data.data.length/2)
          var d = res.data.data.length-1
          if(a == 0){
            e.push(0)
          }else if(a == c){
            e.push(c)
          }else if(a == d){
            e.push(d)
          }else{
            e.push(b)
          }
        }
        that.xday = e
        for(var f = 0 ; f<res.data.data.length ; f++){
          g.push(res.data.data[f].numOne)
          h.push(res.data.data[f].numTwo)
        }
        that.yvone = g
        that.yvtwo = h
        that.bbar()
      })
    }
  },
  // - this
  created() {

  },
  // - DOM
  mounted() {
    this.bbar();
    this.ggetusergrowth()
  },
//beforeCreate() {}, // - 
//beforeMount() {}, // - 
//beforeUpdate() {}, // - 
//updated() {}, // - 
//beforeDestroy() {}, // - 
//destroyed() {}, // - 
//activated() {}, //keep-alive
}
</script>
<style lang="less">
.Usergrowth{
  width: 100%;
  height: 110%;
  
  #growthtu{
    width: 416px;//525
    height: 280px;//260

  }
}
</style>